
import { defineComponent } from "vue";
import CheerStick from "@/components/CheerStick.vue";
import Background from "@/components/BackGround.vue";
// import Settings from "@/components/SettingsDropdown.vue";
import G_Modal from "@/components/GoogleLoginModal.vue";
import A_Modal from "@/components/GoogleLoginModal.vue";

export default defineComponent({
  components: {
    CheerStick,
    Background,
    G_Modal,
    A_Modal,
  },
  data() {
    return {
      level: 1,
      state: 1,
      type: "stick",
    };
  },
  mounted() {
    setInterval(() => {
      this.state++;
      if (this.state > 10) {
        this.state = 1;
      }
      console.log(this.state + " " + this.level);
    }, 1000);
  },
});
