import { createRouter, createWebHashHistory, RouteRecordRaw } from "vue-router";
import SenderView from "../views/SenderView.vue";
import Settings from "@/components/SettingsDropdown.vue";
import Footer from "@/components/BgFooter.vue";
import RC from "@/views/ReceiverStick.vue";
import gm from "@/views/testGM.vue";

const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    name: "home",
    component: SenderView,
  },
  {
    path: "/settings",
    name: "settings",
    component: Settings,
  },
  {
    path: "/footer",
    name: "footer",
    component: Footer,
  },
  {
    path: "/RC",
    name: "rc",
    component: RC,
  },
  {
    path: "/gm",
    name: "gm",
    component: gm,
  },
];

const router = createRouter({
  history: createWebHashHistory(), // Use createWebHashHistory instead of createWebHistory
  routes,
});

export default router;
